.popup {
  width: 480px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 32px 32px 40px 48px;
  border-radius: 8px;
  color: #484848;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }
  @media (max-width: 767px) {
    .popup {
      width: 100%;
      border-radius: 0;
      box-shadow: none;
      top: 0;
      left: 0;
      transform: none;
      padding: 24px 16px 292px 16px; } }
  .popup .popup__close {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    padding: 0;
    overflow: visible;
    z-index: 10;
    margin-left: auto;
    display: block; }
    .popup .popup__close::before {
      content: url("../img/icon-close.png"); }
  .popup p {
    margin-top: 0;
    font-size: 15px;
    line-height: 25px; }
    @media (max-width: 767px) {
      .popup p {
        font-size: 14px;
        line-height: 22px; } }
  .popup h3 {
    font-size: 28px;
    line-height: 34px;
    font-weight: 700;
    color: #1fc0fc;
    letter-spacing: -2%;
    margin: 0;
    margin-bottom: 16px; }
    @media (max-width: 767px) {
      .popup h3 {
        font-size: 24px;
        line-height: 29px; } }
  .popup--callback {
    display: none;
    z-index: 110; }
    .popup--callback--show {
      display: block; }
    .popup--callback p {
      padding-right: 40px; }
    @media (max-width: 767px) {
      .popup--callback h3 {
        margin-top: 128px; }
      .popup--callback p {
        padding-right: 0; }
      .popup--callback h3,
      .popup--callback p {
        text-align: center; } }
  .popup--success {
    display: none; }
    .popup--success--show {
      display: block; }
    .popup--success img {
