      position: absolute;
      top: 5px;
      left: 15px;
      width: 16px;
      height: 16px;
      border: 2px solid #ffc341;
      border-image: linear-gradient(to right top, #ffd701, #ffc341, #ffd701);
      border-radius: 2px;
      border-image-slice: 1; }
  .callback-form .callback-form__checkbox:checked + label {
    position: relative; }
    .callback-form .callback-form__checkbox:checked + label::before {
      content: url("../img/icon-tick.svg");
      position: absolute;
      top: 2px;
      left: 17px;
      z-index: 11; }

